import React, { PureComponent } from 'react'
import { compose } from 'redux'
import { connect } from 'react-redux'
import withTranslations from 'Shared/hoc/withTranslations'
import { withRouter } from 'react-router-dom'
import { DEFAULT_REDIRECT_DELAY } from 'Shared/constants/Global'
import withScripts from 'TrackingScripts'
import { veroEvents } from 'TrackingScripts/scripts/candidate/vero'
import { pathOr, propOr } from 'ramda'
import { Roles } from 'Shared/constants/User'
import { getQueryKeys } from 'Shared/helpers/query.js'
import './ThankYouPage.scss'

class ThankYouPage extends PureComponent {
  state = {
    ready: false,
    veroEventSent: false,
    secondsRemaining: DEFAULT_REDIRECT_DELAY
  }

  countSeconds = () => this.setState({ secondsRemaining: this.state.secondsRemaining - 1 })

  componentDidMount () {
    this.setState({ ready: true })
    this.secondsInterval = setInterval(this.countSeconds, 1000)
  }

  componentDidUpdate (prevProps, prevState) {
    if (prevState.secondsRemaining === 1 && this.state.secondsRemaining === 0) {
      clearInterval(this.secondsInterval)
      this.redirectUser()
    }

    const shouldTriggerVeroEvent = !!document.getElementById('script-vero') &&
      pathOr(false, ['history', 'location', 'state', 'id'], this.props) &&
      !this.state.veroEventSent

    if (shouldTriggerVeroEvent) {
      veroEvents.signUpConfirmed(this.props.history.location.state.id)
      this.setState({ veroEventSent: true })
    }
  }

  redirectUser () {
    switch (true) {
      case this.props.isAuthenticated && this.props.userRole === Roles.employer.name:
        const queryParams = getQueryKeys(this.props.location.search)
        const checkoutParam = propOr('', 'selfCheckout', queryParams)
        const hasSelectedJobPosts = checkoutParam === 'jobPosts'
        return this.props.history.push(hasSelectedJobPosts ? '/pricing/job-posts/review' : '/employer/job-offers')
      case this.props.isAuthenticated && this.props.userRole === Roles.candidate.name:
        return this.props.history.push('/candidate/profile/complete')
      default:
        const urlRole = this.props.match.path.split('/')[1]
        return this.props.history.push(`/${urlRole}/login`)
    }
  }

  render () {
    const { t } = this.props
    const urlRole = this.props.match.path.split('/')[1]

    return (
      <div className='thank-you-page__container parsing--hidden'>
        <div className='thank-you-page'>
          <div className='thank-you-page__wrapper'>
            <div className='thank-you-page__content'>
              <i className='icon-im-success thank-you-page__icon' />
              <h2 className='thank-you-page__heading'>{t(`thankYouPage.${urlRole}.header`)}</h2>
              <p className='thank-you-page__text'>{t(`thankYouPage.${urlRole}.description`, { secondsRemaining: this.state.secondsRemaining })}</p>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  isAuthenticated: state.user.isAuthenticated,
  userRole: state.user.role
})

export default compose(
  withRouter,
  withScripts,
  withTranslations,
  connect(mapStateToProps)
)(ThankYouPage)



